.App {
  text-align: center;
}

.tabs {
  display: block;
  background-color: #222;
  list-style-type: none;
  li {
    display: inline-block;
    padding: 12px;
    font-weight: bold;
    color: #aaa;
    cursor: pointer;
    &:hover {
      color: #ddd;
    }
    &.active {
      color: #eee;
    }
  }
}

table {
  margin-left: auto;
  margin-right: auto;
  th {
    background-color: #222;
  }
  td, th {
    padding: 1px 4px;
    border: 1px solid #aaa;
  }
  input, select {
    position: relative;
    top: -1px;
  }
}

.header {
  span {
    margin: 0 4px;
    input {
      padding: 0 2px;
      width: 65px;
    }
  }
  code {
    display: inline-block;
    padding: 4px;
    border: 1px solid #aaa;
    background-color: #222;
  }
  margin-bottom: 12px;
}

.RankPlanner {
  padding: 1em;
  table {
    td:nth-child(1) {
      text-align: right;
    }
    td:nth-child(2) {
      text-align: right;
    }
    td:nth-child(3) {
      text-align: left;
    }
  }
}

.HonorCap {
  padding: 1em;
  .honorTable {
    thead tr th {
      vertical-align: middle;
    }
    thead tr:first-child th span {
      display: inline-block;
      width: 60px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
    .confirmed {
      color: #0f0;
    }
    .probably {
      color: #ff0;
    }
    .used {
      background-color: #666;
    }
    .hover {
      background-color: #f66;
    }
    margin-bottom: 1em;
  }
  input {
    width: 65px;
    padding: 0 2px;
  }
  .warning {
    display: block;
  }
  .results {
    margin-top: 1em;
    max-height: 800px;
    overflow-y: auto;
    ul {
      border: 1px solid #aaa;
      background-color: #222;
      padding: 4px 20px 4px 30px;
      list-style-type: none;
      display: inline-block;
    }
    li {
      position: relative;
      &.empty {
        color: #ff0;
      }
      &:not(.empty)::before {
        content: '\25B8';
        position: absolute;
        left: -18px;
      }
      text-align: left;
      cursor: default;
      &:not(.empty):hover {
        background-color: #f66;
      }
    }
  }
}
